@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600;700&display=swap');

.App {
  display: flex;
  flex-direction: column;
  align-items: flex-start;     /* align to the left horizontally */
  justify-content: flex-start; /* align to the top vertically */
  height: 100vh;
  padding-top: 120px;          /* move typing text downward */
  padding-left: 10vw;          /* space from the left edge */
  background-color: white;
}

.typewriter {
  font-family: 'Quicksand', sans-serif;
  font-size: 1.5rem; /* smaller font size */
  color: #333;
}

.cursor {
  display: inline-block;
  width: 2px;
  height: 1.5rem; /* same height as your text */
  background-color: black;
  margin-left: 2px;
  animation: blink 1s step-end infinite;
}

@keyframes blink {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
